.lock-combination {
  position: relative;
  width: 500px;
  height: 100px;
  padding: 80px 30px;
  margin: 15px 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #737478;
  background: -webkit-gradient(linear, left top, left bottom, from(#737478), to(#212528));
  background: -webkit-linear-gradient(top, #737478 0%, #212528 100%);
  background: linear-gradient(180deg, #737478 0%, #212528 100%);
  border: 3px solid #eef2f3;
  border-radius: 15px;
  box-shadow: inset 0 0 15px 5px #171515, 0 0 15px 5px #171515; }
  .lock-combination::before, .lock-combination::after {
    content: '';
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 0;
    height: 0;
    border-style: solid; }
  .lock-combination::before {
    left: 0;
    border-width: 5px 0 5px 7px;
    border-color: transparent transparent transparent #eef2f3; }
  .lock-combination::after {
    right: 0;
    border-width: 5px 7px 5px 0;
    border-color: transparent #eef2f3 transparent transparent; }

.question {
  position: absolute;
  bottom: calc(100% + 30px);
  width: 100%; }
